import React, { Component } from "react";

export default class Footer extends Component {
  componentDidMount() {
      console.log("HEI IM FINISHED LOADING FROM fOOOTER")
      // Get all "navbar-burger" elements
      var $navbarBurgers = Array.prototype.slice.call(document.querySelectorAll('.navbar-burger'), 0);
    
      // Check if there are any navbar burgers
      if ($navbarBurgers.length > 0) {
    
        // Add a click event on each of them
        $navbarBurgers.forEach(function ($el) {
          $el.addEventListener('click', function () {
    
            // Get the target from the "data-target" attribute
            var target = $el.dataset.target;
            var $target = document.getElementById(target);
            console.log("HEI BURGER CLICKED")
            // Toggle the class on both the "navbar-burger" and the "navbar-menu"
            $el.classList.toggle('is-active');
            $target.classList.toggle('is-active');
    
          });
        });
      }
    
  }
  render() {
    return (
      <footer className="footer">
        <div className="container">
          <div className="content has-text-centered">
            <p className="has-text-white">
              <strong className="has-text-white">
                Selebtwit Statistics Analysis
              </strong>{" "}
              by
              <a className="has-text-white" href="http://twitter.com/nirzaq">
                {" "}
                @nirzaq
              </a>.
              {/* <p className="has-text-white">Update Terakhir :
            <script type="text/javascript">{document.write(document.lastModified)}</script>
          </p> */}
            </p>
          </div>
        </div>
      </footer>
    );
  }
}



