import React from "react"
import Link from "next/link";

const TopSevenTweet = props => {
  return (
    <Link as={`/selebtwits/selebtwit/${props.username}`} href={`/selebtwit?username=${props.username}`}>
      <a className="is-info is-size-7 Topseven">Lihat 20 Top Tweets </a>
    </Link>
  );
};

export default TopSevenTweet


