import React from "react";
import Link from "next/link";

const Header = () => (
  <nav className="navbar has-shadow is-fixed-top">
    <div className="navbar-brand">
      <a
        className="navbar-item navbar-logo"
        href="https://nirzaq.github.io/selebtwits/"
      >
        <img
          src={process.env.BACKEND_URL + "/static/twitter_circle_logo.svg"}
          alt="Selebtwit Statistics Analysis"
          width="50"
          height="28"
        />
        <strong>#SSA</strong>
      </a>
      <div className="navbar-menu" />
      <div
        className="navbar-burger burger"
        data-target="navbarExampleTransparentExample"
      >
        <span />
        <span />
        <span />
      </div>
    </div>

    <div id="navbarExampleTransparentExample" className="navbar-menu">
      <div className="navbar-start">
        <Link href="/faq" as={process.env.BACKEND_URL + "/faq"}>
          <a className="navbar-item">FAQ</a>
        </Link>
      </div>
    </div>
  </nav>
);

export default Header;



