import React, { Component } from "react";
import { DebounceInput } from "react-debounce-input";

import Layout from "../components/Layout";
import LeaderboardItem from "../components/LeaderboardItem";
import indonesia from "../data/indonesia.json";
import Footer from "../components/Footer";
import Header from "../components/Header";

export default class Index extends Component {
  constructor(props) {
    super(props);
    let data = indonesia;
    this.state = {
      allData: data,
      currentData: data,
      loadedItem: 40,
      isLoading: false
    };
  }
  componentDidMount() {
    if ("serviceWorker" in navigator) {
      navigator.serviceWorker
        .register("/service-worker.js")
        .then(registration => {
          console.log("service worker registration successful");
        })
        .catch(err => {
          console.warn("service worker registration failed", err.message);
        });
    }
  }
  render() {
    return (
      <Layout>
        <div className="site">
          <div className="site-content">
            <Header />
            <div className="container">
              <h1 className="App-title is-size-2 has-text-black has-text-centered">
                <span className="icon">
                  <img src="static/twitter_circle_logo.svg" alt="#SSA" />
                </span>
                <strong> Selebtwit </strong>
                <span className="icon flag">
                  <img src="static/id.svg" alt="Indonesia" />
                </span>
              </h1>
              <ul className="leaderboard">
                <li
                  key="search-box"
                  className="box is-marginless has-text-centered is-shadowless field has-addons is-fixed-top"
                >
                  <div className="control is-expanded">
                    <DebounceInput
                      className="input is-expanded"
                      placeholder="Cari @Username"
                      id="search"
                      debounceTimeout={300}
                      onChange={this.handleSearch}
                    />
                  </div>
                </li>
                {this.state.currentData
                  .slice(0, this.state.loadedItem)
                  .map((selebtwit, index) => {
                    return <LeaderboardItem {...selebtwit} />;
                  })}
                {this.state.loadedItem > this.state.allData.length ||
                this.state.currentData.length < 20 ||
                this.state.currentData.length < this.state.loadedItem ? (
                  ""
                ) : (
                  <li
                    key="load_button"
                    className="load-button has-text-centered is-shadowless"
                  >
                    <a className="button is-info" onClick={this.handleLoadMore}>
                      Load More
                    </a>
                  </li>
                )}
              </ul>
            </div>
          </div>
          <Footer />
        </div>
      </Layout>
    );
  }

  handleLoadMore = e => {
    e.preventDefault();
    let loadedItem = this.state.loadedItem;
    let newLoaded = loadedItem + 40;
    console.log("Click Load More Button! with end");
    this.setState({
      loadedItem: newLoaded
    });
  };

  handleSearch = e => {
    e.preventDefault();
    var term = e.target.value;
    var re = new RegExp(term, "ig");

    let data = this.state.allData.filter((seleb, index) => {
      return seleb.username.match(re);
    });

    this.setState({
      currentData: data
    });

    if (term.length < 1) {
      console.log("Zero term");
      this.setState({
        loadedItem: 40
      });
    }
  };
}



